@use 'base/variables' as v;
@use 'base/mixins' as m;

.descargar {
    background-color: v.$grisClaro;
    padding: v.$separacion 0; 
    &__grid {
        @include m.tablet { 
            @include m.grid(2, v.$separacion);    
        }
    }
    &__imagen {
        width: 50%;
        margin: 0 auto;
        transition: transform .3s ease;
        &:hover {
            transform: rotate(4deg) scale(1.2);
        }
    }
}